import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    title:"",
    userName:"",
    list:"3",
    carts:[],
    barcodeId:"",
  },
  getters: {
  },
  mutations: {
    SET_LIST_NUM(state,data){
      state.list = data
    },
    ADD_CART_MENU(state,data){
      state.carts.append(data)
    },
    CHANGE_BARCODE_ID(state,data){
      state.barcodeId = data
    },
    CHANGE_ITEM_TITLE(state,data){
      state.title = data
    },
    ADD_ITEM(state,data){
      state.carts.push(data)
    },
    
  },
  actions: {
  },
  modules: {
  }
})
